body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100%;
}

.game {
  width: 300px;
}

.bomb-wrapper {
  display: -webkit-flex;
  display: flex;
  /* justify-content: flex-start; */
  -webkit-align-items: flex-end;
          align-items: flex-end;
  /* width: 100%; */
  height: 100px;
  background: gray;
  outline: 1px solid #888888;
  height: 3em;
  /* padding-left: 30px; */
  padding-top: 30px;
  
}

.calc-wrapper {
  width: 100%;
  height: 400px;
}

.row {
  display: -webkit-flex;
  display: flex;
  width: 100% 
}

.button-wrapper {
    height: 3em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #e0e1e6;
    color: #888;
    -webkit-flex: 1 1;
            flex: 1 1;
    outline: 1px solid #888888;
}

.operator {
    background-color: #fe9241;
    color: #ffffff;
}
.input {
    height: 3em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #000000;
    color: #ffffff;
    padding: 0.5em;
    outline: 1px solid #888888;
}
.clear-btn {
    height: 3em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #c5c3cd;
    color: #000000;
    -webkit-flex-basis: 0;
            flex-basis: 0;
    -webkit-flex-grow: 3;
            flex-grow: 3;
    outline: 1px solid #888888;
}
.back-btn {
    height: 3em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #c5c3cd;
    color: #000000;
    -webkit-flex-basis: 0;
            flex-basis: 0;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    outline: 1px solid #888888;
}
img {
    height: 50px;
}

.timer {
    text-align: center;
    color: white;
}

.value {
    padding-bottom: 10px;
    font-size: 24px;
}

.bomb {
    padding-left: 34px;
    text-align: center;
}
